.title-body .header {
	position: absolute;
	padding-left: 5%;
	padding-right: 5%;
	width: 90%;
	top: 0;
	left: 0;
	.container {
		display: flex;
		justify-content: space-between;
		padding: 5%;
	}
	a {
		color: $black;
	}
	.logo {
		font-weight: bold;
	}
	@media only screen and (max-width: 767px) {
		position: relative;
	}
}